// ** Third Party Components
import classnames from 'classnames'
import PerfectScrollbar from 'react-perfect-scrollbar'
import Avatar from '@components/avatar'
// ** Reactstrap Imports
import { Camera, Check, Edit2, Plus, Trash2, X } from 'react-feather'
import { useEffect, useState } from 'react'
import { Button, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import Swal from 'sweetalert2'
import { deleteGroup } from './store/index'
import withReactContent from 'sweetalert2-react-content'
import { activeOrganization, activeOrganizationid, orgUserId } from '@src/helper/sassHelper.js'
import { useDispatch } from 'react-redux'
const activeOrg = activeOrganization()
const MySwal = withReactContent(Swal)
const userId = orgUserId()
const activeOrgId = activeOrganizationid()

const UserProfileSidebar = props => {

  // ** Props
  const dispatch = useDispatch()
  const { handleGroupInfoSidebarRight, userGroupInfoRight, refreshList, user, userUpdate, updateSelectedUser, store } = props
  const { selectedUser, contacts } = store

  const profileImg = require('@src/assets/images/avatars/avatar-blank.png').default
  const [editFlag, setEditFlag] = useState(false)
  const [groupName, setGroupName] = useState(selectedUser?.name)
  const [imageId, setImageId] = useState(selectedUser.imageid)
  const [formModal, setFormModal] = useState(false)
  const [userList, setUserList] = useState([])
  const [contactList, setContactList] = useState(contacts)
  const [groupUserList, setGroupUserList] = useState()

  const updateGroupName = () => {
    axios.post('/chatmessages/updategroupname', { id: selectedUser.id, groupName })
      .then(() => {
        setEditFlag(false)
        refreshList(true)
        const temp = { ...selectedUser }
        temp.name = groupName
        userUpdate(temp)

      })
  }

  useEffect(() => {
    const temp = updateSelectedUser.users.map((obj) => obj.id)
    setGroupUserList(temp)
    setImageId(updateSelectedUser.imageid)
    setGroupName(updateSelectedUser.name)
  }, [updateSelectedUser])

  const deletGroupfn = () => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteGroup({ id: selectedUser.id }))
        handleGroupInfoSidebarRight()
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Group has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })

  }

  const uploadFiles = (file) => {

    const formData = new FormData()
    formData.append(`files[0]`, file)
    formData.append('organizationId', activeOrgId)
    formData.append('id', selectedUser.id)
    formData.append('createdBy', userId)
    axios.post('/google/grouplogoUploads', formData).then((res) => {
      setImageId(res.data.attachment_id)
      const temp = { ...selectedUser }
      temp.imageid = res.data.attachment_id
      userUpdate(temp)

    }).catch((err) => { console.log(err) })
  }

  const onChange = e => {
    const reader = new FileReader(),
      files = e.target.files
    reader.onload = function () {
      setAvatar(reader.result)
    }
    reader.readAsDataURL(files[0])

    uploadFiles(e.target.files[0])

  }

  const closeEditGroup = () => {
    setFormModal(false)
  }

  const handleChange = (e) => {
    if (e.target.checked) {
      const temp = [...userList]
      temp.push(e.target.value)
      setUserList(temp)
    } else {
      const temp1 = [...userList]
      temp1.splice(temp1.indexOf(e.target.value), 1)
      setUserList(temp1)
    }
  }

  const renderContacts = () => {

    const arrToMap = contactList.filter((obj => groupUserList.indexOf(obj.id) === -1))
    return arrToMap.map(item => {
      return (
        <li key={item.id} className='d-flex'>
          <Input type='checkbox' defaultChecked={selectedUser.users.find((obj) => obj.id === item.id)} id={`user_${item.id}`} className='me-1' color='priamry' value={item.id} onChange={(e) => { handleChange(e) }} />
          <div className='chat-info flex-grow-1 position-relative ms-50'>
            <div className='d-flex justify-content-between'>
              <h5 className='mb-0 text-truncate font-small-4'>{item.name}</h5>
            </div>
          </div>
        </li>
      )
    })

  }

  const updateGroup = () => {

    axios.post('/chatmessages/updategroup', { id: selectedUser.id, userList: [...groupUserList, ...userList] })
      .then((res) => {
        setEditFlag(false)
        refreshList(true)
        const temp = { ...selectedUser }
        temp.users = res.data.users
        userUpdate(temp)
        setFormModal(false)
      })
  }

  const deleteUser = (id) => {

    const temp = [...contactList]
    temp.splice(temp.indexOf(id), 1)
    setContactList(temp)

  }

  return (
    <div className={classnames('user-profile-sidebar col-md-3 col-sm-12', { show: userGroupInfoRight === true })}>
      <PerfectScrollbar className='h-100 user-profile-sidebar-area p-1' options={{ wheelPropagation: false }}>
        <header className='user-profile-header'>
          <div className='d-flex justify-content-between border-1 border pt-1 px-1 pb-50 align-items-center'>
            <h6 className='text-body'>Group Info</h6>
            <span className='close-icon' onClick={handleGroupInfoSidebarRight}>
              <X size={14} />
            </span>
          </div>
        </header>

        <div className='header-profile-sidebar text-center m-50 border border-start-0 border-end-0 border-top-0'>
          <Avatar
            className='box-shadow-1 avatar-border'
            size='xl'
            img={imageId ? `https://drive.google.com/thumbnail?id=${imageId}&authuser=0` : profileImg}
            imgHeight='40'
            imgWidth='40'
          />

          <Button.Ripple size={'sm'} tag={Label} className='cursor-pointer btn-icon rounded-circle' outline color='flat-success'>
            <Camera size={21} />
            <Input type='file' onChange={onChange} hidden accept='image/*' />
          </Button.Ripple>

          {!editFlag &&
            <div className='d-flex mt-1 justify-content-center w-100'>
              <h5 className='chat-user-name'>{groupName}</h5>
              <Edit2 className='ms-1' onClick={() => { setGroupName(user?.name); setEditFlag(true) }} size={14} />
            </div>
          }
          {editFlag &&
            <div className='d-flex mt-50 justify-content-center w-100 align-items-center border border-2 rounded-0 border-top-0 border-start-0 border-end-0'>
              <Input className='border border-0' type='text' value={groupName} onInput={(e) => { setGroupName(e.target.value) }} />
              <X className='ms-50 cursor-pointer' onClick={() => { setEditFlag(false); setGroupName(user.name) }} size={24} />
              <Check className='ms-50 cursor-pointer' onClick={() => { updateGroupName() }} size={24} />
            </div>
          }
          <p className='font-small-3'>Group. {selectedUser?.users?.length} members</p>
        </div>

        <div className='d-flex cursor-pointer justify-content-start align-items-center m-1 pb-1 border border-1 border-top-0 border-start-0 border-end-0' onClick={() => setFormModal(true)}>
          <Avatar className='me-1' img={profileImg} imgHeight='30' imgWidth='30' />
          <div className='profile-user-info w-100'>
            <div className='d-flex align-items-center w-100'>
              <Plus size={15} className='' />
              <p className='mb-0 ms-25'>Add Member</p>
            </div>
          </div>
        </div>

        {updateSelectedUser?.users?.map((obj, k) => {
          return (
            <div className='d-flex justify-content-start align-items-center m-1' key={k}>
              <Avatar className='me-1' img={obj?.imageid ? `https://drive.google.com/thumbnail?id=${obj?.imageid}&authuser=0` : profileImg} imgHeight='30' imgWidth='30' />
              <div className='profile-user-info w-100'>
                <div className='d-flex justify-content-between w-100'>
                  <p className='mb-0'>{obj?.name}</p>
                  {
                    parseInt(activeOrg.rolelevel) === 1  &&
                    <Trash2 className='text-danger mt-25' size={14} onClick={() => { deleteUser(obj.id) }} />
                  }
                </div>
              </div>
            </div>
          )
        })
        }
        <Row className='p-2'>
          <Button outline color='danger' onClick={() => { deletGroupfn() }}>Delete</Button>
        </Row>
      </PerfectScrollbar>
      {formModal &&
        <Modal isOpen={formModal} toggle={() => { setFormModal(!formModal); closeAddGroup(!formModal) }} className='modal-dialog-centered'>
          <ModalHeader toggle={() => closeEditGroup()}>
            Add Member
          </ModalHeader>
          <ModalBody>
            <PerfectScrollbar className='chat-user-list-wrapper list-group' options={{ wheelPropagation: false }}>
              <h6 className='text-primary mt-1'>Users</h6>
              <ul className='chat-users-list contact-list media-list p-0'>{renderContacts()}</ul>
            </PerfectScrollbar>
          </ModalBody>
          <ModalFooter>
            <Button color='warning' onClick={() => closeEditGroup(false)}>Cancel</Button>
            <Button color='primary' onClick={() => updateGroup()}>Save</Button>
          </ModalFooter>
        </Modal >
      }
    </div >
  )
}

export default UserProfileSidebar
