// ** Custom Components
import { useState } from 'react'
import { Button, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { orgUserId, activeOrganizationid } from '@src/helper/sassHelper'
import { getChatGroups } from './store'
import { useDispatch } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'
let userId = orgUserId()
let orgId = activeOrganizationid()
// ** Third Party Components
const Chatgroup = props => {
  // ** Props & Store
  const [formModal, setFormModal] = useState(false)
  const dispatch = useDispatch()
  const { contacts, closeAddGroup, addgroup } = props
  const [groupName, setGroupName] = useState('')
  const [selContacts, setSelectedContact] = useState([])

  const handleChange = (e) => {
    const temp = [...selContacts]
    if (e.target.checked) {
      if (temp.indexOf(e.target.value) === -1) {
        temp.push(e.target.value)
      }
    } else {
      temp.splice(temp.indexOf(e.target.value), 1)
    }

    setSelectedContact(temp)
  }

  const renderContacts = () => {
    if (contacts && contacts.length) {
      const arrToMap = contacts
      return arrToMap.map(item => {
        return (
          <li className='d-flex align-item-center my-1' key={item.name} >
            <Input type='checkbox' defaultChecked={userId === item.id} id={`user_${item.id}`} className='me-1' color='priamry' value={item.id} onChange={(e) => { handleChange(e) }} />
            <div className='chat-info flex-grow-1'>
              <Label className='mb-0 ms-1' for={`user_${item.id}`}>{item.name}</Label>
              {/* <CardText className='text-truncate'>{item.about}</CardText> */}
            </div>
          </li>
        )
      })

    }
  }
  const [errflag, setErrflag] = useState(false)
  const createGroup = () => {

    if (groupName === '' || selContacts.length < 2) {
      setErrflag(true)
      return
    }
    const obj = {
      name: groupName,
      userIds: selContacts,
      createdBy: userId,
      organizationId: orgId
    }

    axios.post('/chatmessages/creategroup', obj)
      .then(() => {
        dispatch(getChatGroups())
        closeAddGroup(false)
        setErrflag(false)
      })
      .catch(() => { })
  }

  const closeModal = () => {
    setFormModal(false)
    closeAddGroup(false)
  }

  useState(() => {
    userId = orgUserId()
    orgId = activeOrganizationid()
    setFormModal(addgroup)
    setSelectedContact([userId])
  }, [addgroup])

  return (
    <Modal isOpen={formModal} toggle={() => { setFormModal(!formModal); closeAddGroup(!formModal) }} className='modal-dialog-centered'>
      <ModalHeader toggle={() => closeModal()}>
        Add Group
      </ModalHeader>
      <ModalBody>
        {
          errflag && <p className='text-danger'>Please Enter Name, atleast to person need to create Group.</p>
        }
        <Input type='text' placeholder='Group Name *' onChange={(e) => { setGroupName(e.target.value) }} />
        <PerfectScrollbar className='chat-user-list-wrapper list-group' options={{ wheelPropagation: false }}>
          <h6 className='text-primary mt-1'>Users</h6>
          <ul className='chat-users-list contact-list media-list p-0'>{renderContacts()}</ul>
        </PerfectScrollbar>
      </ModalBody>
      <ModalFooter>
        <Button color='warning' onClick={() => closeAddGroup(false)}>Cancel</Button>
        <Button color='primary' onClick={() => createGroup()}>Add</Button>
      </ModalFooter>
    </Modal >
  )
}

export default Chatgroup
