// ** React Imports
import { useState, useEffect, Fragment, useRef } from 'react'
import axios from '@src/configs/axios/axiosConfig'
// ** Store & Actions
import { sendMsg } from './store'
import { useDispatch } from 'react-redux'

// ** Third Party Components
import { useDropzone } from 'react-dropzone'
import { Send, Paperclip, Smile, File, X } from 'react-feather'
import Picker from '@emoji-mart/react'
import data from '@emoji-mart/data'
// ** Reactstrap Imports
import {
  Form,
  Label,
  Input,
  Button,
  Col,
  Card,
  CardBody,
  Progress
} from 'reactstrap'
const fileimg = require('@src/assets/images/icons/unknown.png').default
import { orgUserId, activeOrganizationid } from '@src/helper/sassHelper'

const orgId = activeOrganizationid()
const userId = orgUserId()
const Attachment = props => {
  // ** Props & Store
  const { attachmentArr, selectedUser, setCloseAttachment } = props
  const [fileprogress, setProgress] = useState(0)
  const [filesArr, setFilesArr] = useState([])
  const [tempFile, setTempFile] = useState([])
  // ** Refs & Dispatch
  //const chatArea = useRef(null)
  const [preview, setPreview] = useState('')
  const [selFile, setSelFile] = useState({})

  const dispatch = useDispatch()
  const domInputRef = useRef()
  const [attachmentId, setAttachmentId] = useState([])

  useEffect(() => {
    if (attachmentArr.length > 0) {
      const file = attachmentArr[0]
      setFilesArr(attachmentArr)
      setSelFile(file)
      setTempFile(attachmentArr)
      const objectUrl = URL.createObjectURL(file)
      setPreview(objectUrl)
    }
  }, [attachmentArr])
  // ** State
  const [msg, setMsg] = useState('')

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const uploadFiles = (files) => {

    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('organizationId', orgId)
    formData.append('createdBy', userId)
    axios.post('/google/chatmessage', formData, configUpload).then((res) => {
      setAttachmentId([...attachmentId, ...res.data.attachment_ids])
      setTempFile([])
    }).catch((err) => { console.log(err) })
  }

  useEffect(() => {
    if (tempFile.length > 0) {
      uploadFiles(tempFile)
    }
  }, [tempFile])

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        setTempFile(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const closeAttachment = () => {
    setPreview('')
    setSelFile({})
    setAttachmentId([])
    setFilesArr([])
    setCloseAttachment(true)
  }
  const [emojiOpen, setEmojiOpen] = useState(false)
  // ** Sends New Msg
  const handleSendMsg = e => {
    e.preventDefault()
    if (msg !== '' || attachmentId.length > 0) {
      dispatch(sendMsg({ usertype: selectedUser.usertype, toUserId: selectedUser.id, fromUserId: userId, organizationId: orgId, message: msg, attachmentIds: attachmentId }))
      if (domInputRef.current) {
        domInputRef.current.innerText = ""
      }
      closeAttachment()
      setMsg('')
    }
  }

  const previewFn = (file) => {
    setSelFile(file)
    const objectUrl = URL.createObjectURL(file)
    setPreview(objectUrl)
  }

  const previewImg = (file) => {

    const objectUrl = URL.createObjectURL(file)
    return objectUrl
  }

  const myCallback = (val) => {
    const temp = `${domInputRef.current.innerHTML}${`<span>${val.native}</span>`}`
    domInputRef.current.innerHTML = temp
    setMsg(temp)
  }


  return (

    <Card className='attachment-window p-2'  >
      <CardBody>
        <Col className='text-center'>
          {selFile?.name}
          <X size={18} className='text-end' onClick={() => closeAttachment()} />
        </Col>
        <div {...getRootProps({ className: 'dropzone' })} >
          <Col md='8' sm='12' className='m-auto' >
            {selFile?.extension === 'png' &&
              <Col className='text-center'>
                <img referrerPolicy="no-referrer" className='img-fluid width-60-per p-2' src={preview} />
              </Col>
            }
            {selFile?.extension !== 'png' &&
              <Col className='text-center'>
                <img referrerPolicy="no-referrer" className='img-fluid width-25-per p-2' src={fileimg} />
              </Col>
            }
          </Col>
          <Col md={10} className='d-flex m-auto justify-content-center'>
            {filesArr.map((file, k) => {
              return (
                <Fragment key={k}>
                  {file?.extension === 'png' ? <img className='img-tumbnail width-15-per p-1' src={previewImg(file)} onClick={() => { previewFn(file) }} /> : <img src={fileimg} className='img-tumbnail width-10-per p-1' onClick={() => { previewFn(file) }} />}
                </Fragment>
              )
            })
            }
          </Col>
        </div>
        {fileprogress > 0 && <Progress color={fileprogress < 100 ? 'primary' : 'success'} value={fileprogress} />}

        <Form className='chat-app-form' onSubmit={e => handleSendMsg(e)}>
          <Col className='chatgroup'>
            <div className='chattools'>
              <div className='px-1 pt-1'>
                {emojiOpen &&
                  <div style={{ position: 'absolute', bottom: '53px' }}>
                    <Picker data={data} onEmojiSelect={myCallback} />
                  </div>
                }
                <Smile className='cursor-pointer text-secondary' size={20} onClick={() => setEmojiOpen(!emojiOpen)} />
              </div>
              <div className='px-1 pt-1'>
                <Label className='attachment-icon mb-0' for='attach-doc'>
                  <Paperclip className='cursor-pointer text-secondary' size={18} {...getRootProps({ className: 'dropzone' })} />
                  <input {...getInputProps()} />
                </Label>
              </div>
            </div>
            <div className='mainEdit w-100 me-1'>
              <div placeholder='Type your message or use speech to text' ref={domInputRef} contentEditable={true} className='border border-s-0 border-e-0 maxheight-100 rounded-2 w-100' onInput={e => { setMsg(e.currentTarget.innerHTML) }}>
              </div>
            </div>
            <Button className='send' color='primary'>
              <Send size={14} className='d-lg-none' />
              <span className='d-none d-lg-block'>Send</span>
            </Button>
          </Col>
        </Form>
      </CardBody>
    </Card>

  )
}

export default Attachment
