// ** React Imports
import { useState, useEffect, useRef, Fragment } from 'react'

// ** Custom Components
import GroupInfo from './GroupInfo'
import Avatar from '@components/avatar'

// ** Store & Actions
import { sendMsg, getChat, chatUpdate } from './store'
import { useDispatch } from 'react-redux'
import FilePr from '@src/helper/driveHelper.js'
// ** Third Party Components
import classnames from 'classnames'
import Picker from '@emoji-mart/react'
import data from '@emoji-mart/data'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { MessageSquare, Menu, Search, Send, Paperclip, File, Smile, ChevronDown, X } from 'react-feather'
import { useDropzone } from 'react-dropzone'
// ** Reactstrap Imports
import {
  Form,
  Label,
  Button,
  Row,
  Col,
  UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem,
  Card
} from 'reactstrap'
import { orgUserId, activeOrganizationid } from '@src/helper/sassHelper'
import Attachment from './Attachment'
import moment from 'moment'

const orgId = activeOrganizationid()
const userId = orgUserId()
const profileImg = require('@src/assets/images/avatars/avatar-blank.png').default

const ChatLog = props => {
  // ** Props & Store  
  const { handleGroupInfoSidebarRight, userGroupInfoRight, handleUser, updateSelectedUser, userSidebarRight, handleUserSidebarRight, handleSidebar, store, userSidebarLeft } = props
  const { userProfile, selectedUser, chatObj, receivedMsg } = store
  const [attachmentArr, setAttachmentArr] = useState([])
  const messageEndRef = useRef(null)
  // ** Refs & Dispatch 
  const chatArea = useRef(null)
  const dispatch = useDispatch()
  const [chatList, setChatList] = useState([])
  const [chatreloadVar, setChatReload] = useState(false)
  const scrollToBottom = () => {
    messageEndRef.current?.scrollIntoView()
  }

  const [userName, setUserName] = useState('')
  const [imageId, setImageId] = useState('')

  useEffect(() => {
    setUserName(selectedUser.name)
    setImageId(selectedUser.imageid)
  }, [selectedUser])

  useEffect(() => {
    setUserName(updateSelectedUser?.name)
    setImageId(updateSelectedUser?.imageid)
  }, [updateSelectedUser])


  // Function to scroll to the middle
  const scrollToMiddle = () => {
    if (chatArea.current && messageEndRef.current) {
      const chatWrapper = chatArea.current
      const messageEnd = messageEndRef.current

      // Calculate the middle position
      const middlePosition = (messageEnd.offsetTop + (chatWrapper.offsetTop ?? 1)) / 2
      // Scroll the chatWrapper to the middle position
      chatWrapper.scrollTop = middlePosition - ((chatWrapper.clientHeight ?? 1) / 2)
    }
    setChatReload(false)
  }

  const [msg, setMsg] = useState('')
  const domInputRef = useRef()

  const [page, setPage] = useState(1)
  // ** If user chat is not empty scrollToBottom
  useEffect(() => {
    const selectedUserLen = Object.keys(selectedUser).length
    if (selectedUserLen > 0) {
      setPage(1)
    }
  }, [selectedUser])

  useEffect(async () => {
    if (Object.keys(receivedMsg).length > 0) {
      if (receivedMsg.fromuserid === selectedUser.id || receivedMsg.touserid === selectedUser.id) {
        const tempmsg = chatList.find((obj) => obj.id === receivedMsg.id)
        if (!tempmsg) {
          const temp = [...chatList, receivedMsg]
          setChatList(temp)
        }
      }
      await dispatch(chatUpdate({ id: selectedUser.id, usertype: parseInt(receivedMsg.usertype), updateflg: true }))
    }
  }, [receivedMsg])

  useEffect(() => {
    if (chatreloadVar) {
      scrollToMiddle()
    } else {
      scrollToBottom()
    }
  }, [chatList])

  useEffect(() => {
    let temp = []
    if (chatObj.refresh) {
      temp = [...chatObj.chats]
    } else {
      temp = [...chatObj.chats, ...chatList]
    }
    setChatList(temp)
  }, [chatObj])

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        setAttachmentArr([...attachmentArr, ...acceptedFiles])
      }
    }
  })

  const [selMessageItem, setSelMessageItem] = useState(null)
  const editReply = (message) => {
    setSelMessageItem(message)
  }

  const closeReply = () => {
    setSelMessageItem(null)
  }

  // ** Opens right sidebar & handles its data
  const handleGroupInfoClick = obj => {
    if (parseInt(obj.usertype) === 1) {
      handleGroupInfoSidebarRight()
      handleUser(obj)
    }
  }

  const handleAvatarClick = obj => {
    handleUserSidebarRight()
    handleUser(obj)
  }

  // ** On mobile screen open left sidebar on Start Conversation Click
  const handleStartConversation = () => {
    if (!Object.keys(selectedUser).length && !userSidebarLeft && window.innerWidth < 992) {
      handleSidebar()
    }
  }

  const setCloseAttachment = () => {
    setAttachmentArr([])
  }
  const [emojiOpen, setEmojiOpen] = useState(false)
  // ** Sends New Msg
  const handleSendMsg = e => {
    e.preventDefault()
    if (msg !== '') {
      dispatch(sendMsg({ usertype: selectedUser.usertype, toUserId: selectedUser.id, fromUserId: userId, organizationId: orgId, message: msg.replaceAll('<div><br></div>', ''), replyid: selMessageItem?.id }))
      setMsg('')
      setEmojiOpen(false)
      setSelMessageItem(null)
      if (domInputRef.current) {
        domInputRef.current.innerText = ""
      }
      
    }
  }

  const chatReload = (e) => {

    if (e.target.scrollTop === 0) {
      setPage(page + 1)
      const data = { id: selectedUser.id, page: page + 1, usertype: selectedUser.usertype }
      dispatch(getChat(data))
      setChatReload(true)
    }

  }

  const myCallback = (val) => {
    const temp = `${domInputRef.current.innerHTML}${`<span>${val.native}</span>`}`
    domInputRef.current.innerHTML = temp
    setMsg(temp)
  }

  // ** ChatWrapper tag based on chat's length
  const ChatWrapper = Object.keys(selectedUser).length && chatObj?.chats ? PerfectScrollbar : 'div'
  const [draggableFlg, setdraggableFlg] = useState(false)
  return (
    <div className={`chat-app-window ${(userSidebarRight || userGroupInfoRight) ? 'col-md-8 col-sm-12' : ''}`} style={{ position: 'relative' }}>
      <div className={classnames('start-chat-area', { 'd-none': Object.keys(selectedUser).length })}>
        <div className='start-chat-icon mb-1'>
          <MessageSquare />
        </div>
        <h4 className='sidebar-toggle start-chat-text' onClick={handleStartConversation}>
          Start Conversation
        </h4>
      </div>
      {Object.keys(selectedUser).length ? (
        <div className={classnames('active-chat', { 'd-none': selectedUser === null })}>
          <div className='chat-navbar'>
            <header className='chat-header'>
              <div className='d-flex align-items-center'>
                <div className='sidebar-toggle d-block d-lg-none me-1' onClick={handleSidebar}>
                  <Menu size={21} />
                </div>
                <Avatar
                  imgHeight='36'
                  imgWidth='36'
                  img={imageId ? `https://drive.google.com/thumbnail?id=${imageId}&authuser=0` : profileImg}
                  className='avatar-border user-profile-toggle m-0 me-1'
                  onClick={() => handleGroupInfoClick(selectedUser)}
                />
                <h6 className='mb-0'>{userName}</h6>
              </div>
              <div className='d-flex align-items-center'>
                <Search size={18} className='cursor-pointer d-sm-block d-none' onClick={() => handleAvatarClick(selectedUser.contact)} />
              </div>
            </header>
          </div>

          <ChatWrapper className='user-chats' onScroll={chatReload} onDragEnter={() => setdraggableFlg(true)}>
            <Row className='chats' ref={chatArea}>
              {
                chatList.map((item, index) => {
                  return (

                    <div
                      key={index}
                      className={classnames('chat', {
                        'chat-left': item.fromuserid !== userId
                      })}
                    >
                      {parseInt(selectedUser.usertype) === 1 && <div className='chat-avatar mt-75'>
                        <Avatar
                          imgWidth={36}
                          imgHeight={36}
                          className='box-shadow-1 cursor-pointer'
                          img={item.touserid !== userId ? userProfile.avatar ?? profileImg : selectedUser.contact?.avatar ?? profileImg}
                        />
                      </div>
                      }

                      <Row className=''>
                        {
                          item?.files?.map((file, k) => {
                            return (
                              <Fragment key={k}>
                                <FilePr key={k} ind={k} file={file} item={item} userId={userId} />
                              </Fragment>
                            )
                          })
                        }
                      </Row>
                      <div className='chat-body me-1'>
                        <div key={index} className='chat-content d-flex flex-wrap flex-column p-50'>
                          {item.replymessage &&
                            <Card className='m-0'>
                              <p className='font-small-2 text-body p-25 bg-light rounded' dangerouslySetInnerHTML={{ __html: item.replymessage?.replaceAll('<div><br></div>', '').replace(/(\\n)/g, "") }}></p>
                            </Card>
                          }
                          <div className='d-flex'>
                            {/* {(parseInt(selectedUser.usertype) === 1 && item.fromuserid !== userId) &&
                              <span className='font-small-2 m position-absolute top-0 text-truncate w-75'>{item.fromuser}</span>
                            } */}
                            <div className={`${(parseInt(selectedUser.usertype) === 1 && item.fromuserid !== userId) ? 'mt-75 d-flex justify-content-between w-100' : 'd-flex justify-content-between w-100'}`}>
                              <p dangerouslySetInnerHTML={{ __html: item.message?.replaceAll('<div><br></div>', '').replace(/(\\n)/g, "") }} ></p>
                              <p className='font-small-1 ms-50 text-end '>{moment(item.createdon, 'x').format('MMM-DD-Y hh:mm a')} </p>
                            </div>
                            <UncontrolledDropdown className={item.touserid === userId ? 'reply-icon-left' : 'reply-icon'}>
                              <DropdownToggle tag='div'>
                                <ChevronDown size={15} className='cursor-pointer text-body' />
                              </DropdownToggle>
                              <DropdownMenu>
                                <DropdownItem
                                  tag='a'
                                  href='/'
                                  className='w-100'
                                  onClick={e => {
                                    e.preventDefault()
                                    editReply(item)
                                  }}
                                >
                                  <span className='align-middle'>Reply</span>
                                </DropdownItem>
                              </DropdownMenu>
                            </UncontrolledDropdown>
                          </div>
                        </div>

                      </div>

                    </div>
                  )
                })
              }
              <div ref={messageEndRef} />
            </Row>
          </ChatWrapper>
          {
            (attachmentArr.length > 0 || draggableFlg) &&
            <Attachment className='attachment-window' attachmentArr={attachmentArr} selectedUser={selectedUser} setCloseAttachment={setCloseAttachment} />
          }
          <Form className={selMessageItem !== null ? 'chat-app-form-with-reply chat-app-form' : 'chat-app-form'} onSubmit={e => handleSendMsg(e)}>
            {selMessageItem !== null &&
              <Row className='d-flex  bg-light-secondary mx-1 mt-50 justify-content-center w-100 px-2 pt-50 row'>
                <Col className='col-11'>
                  <p className='text-truncate mt-50 font-small-3' dangerouslySetInnerHTML={{ __html: selMessageItem.message.replace(/<\/?[^>]+(>|$)/g, " ").substr(0, 20) }}></p>
                </Col>
                <Col><X size={16} onClick={() => closeReply()} /></Col>
              </Row>
            }
            <Col className='chatgroup'>
              <div className='chattools'>
                <div className='px-1 pt-1'>
                  {emojiOpen &&
                    <div style={{ position: 'absolute', bottom: '53px' }}>
                      <Picker data={data} onEmojiSelect={myCallback} />
                    </div>
                  }
                  <Smile className='cursor-pointer text-secondary' size={20} onClick={() => setEmojiOpen(!emojiOpen)} />
                </div>
                <div className='px-1 pt-1'>
                  <Label className='attachment-icon mb-0' for='attach-doc'>
                    <Paperclip className='cursor-pointer text-secondary' size={18} {...getRootProps({ className: 'dropzone' })} />
                    <input {...getInputProps()} />
                  </Label>
                </div>
              </div>
              <div className='mainEdit w-100 me-1'>
                <div placeholder='Type your message or use speech to text' html={msg} ref={domInputRef} contentEditable={true} className='border border-s-0 border-e-0 maxheight-100 rounded-2 w-100' onInput={e => { setMsg(e.currentTarget.innerHTML) }}>
                </div>
              </div>
              <Button className='send' color='primary'>
                <Send size={14} className='d-lg-none' />
                <span className='d-none d-lg-block'>Send</span>
              </Button>
            </Col>
          </Form>
        </div >
      ) : null
      }
    </div >
  )
}

export default ChatLog
