// ** React Imports
import { useState, useEffect } from 'react'

// ** Custom Components
import Avatar from '@components/avatar'
import Chatgroup from './ChatGroup'
// ** Store & Actions
import { selectChat } from './store'
import { useDispatch } from 'react-redux'

// ** Utils
import { isObjEmpty } from '@utils'

// ** Third Party Components
import classnames from 'classnames'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { X, Search, PlusCircle } from 'react-feather'

// ** Reactstrap Imports
import { CardText, InputGroup, InputGroupText, Badge, Input } from 'reactstrap'
import moment from 'moment'

const SidebarLeft = props => {

  const profileImg = require('@src/assets/images/avatars/avatar-blank.png').default
  // ** Props & Store
  const { store, sidebar, handleSidebar, userSidebarLeft } = props
  const { groups, contacts, userProfile, selectedUser } = store

  // ** Dispatch
  const dispatch = useDispatch()

  // ** State
  const [query, setQuery] = useState('')
  const [active, setActive] = useState(0)
  const [filteredChat, setFilteredChat] = useState([])
  const [filteredContacts, setFilteredContacts] = useState([])

  // ** Handles User Chat Click
  const handleUserClick = async data => {
    if (selectedUser.id === data.id) return
    await dispatch(selectChat(data))
    setActive(data.id)
    if (sidebar === true) {
      handleSidebar()
    }
  }

  useEffect(() => {
    if (!isObjEmpty(store.selectedUser)) {
      if (store.selectedUser) {
        setActive(store.selectedUser.id)
      }
    }
  }, [])

  // ** Renders Chat
  const renderGroups = () => {

    if (groups.length === 0) {
      return (
        <li className='no-results show'>
          <h6 className='mb-0'>No Groups Found</h6>
        </li>
      )
    } else {
      const arrToMap = query.length && filteredChat.length ? filteredChat : groups
      return arrToMap.map(item => {
        return (
          <li key={item.id} onClick={() => handleUserClick(item)}
            className={classnames({
              active: active === item.id
            })}>
            <Avatar img={item.imageid ? `https://drive.google.com/thumbnail?id=${item.imageid}&authuser=0` : profileImg} referrerPolicy="no-referrer" imgHeight='42' imgWidth='42' />
            <div className='chat-info flex-grow-1 position-relative'>
              <div className='d-flex justify-content-between'>
                <h5 className='mb-0 text-truncate font-small-4'>{item.name}</h5>
                {item.createdon && <span className='text-end font-small-2 text-body'> {moment(item.createdon, 'x').format('hh:mm a')}</span>}
              </div>
              <CardText className='text-truncate font-small-3' >{item.message?.replace(/(<([^>]+)>)/ig, '')}</CardText>
              {item.unseenMsgs >= 1 ? (
                <Badge className='float-end chat-unreadflg' color='danger' pill>
                  {item.unseenMsgs}
                </Badge>
              ) : null}
            </div>
          </li>
        )
      })
    }
  }

  const profilePath = (id) => {
    if (id) {
      return `https://drive.google.com/thumbnail?id=${id}&authuser=0`
    } else {
      return profileImg
    }
  }

  // ** Renders Contact
  const renderContacts = () => {
    if (contacts && contacts.length) {
      if (query.length && !filteredContacts.length) {
        return (
          <li className='no-results show'>
            <h6 className='mb-0'>No Chats Found</h6>
          </li>
        )
      } else {
        const arrToMap = query.length && filteredContacts.length ? filteredContacts : contacts
        return arrToMap.map(item => {
          return (
            <li key={item.id} className={classnames({
              active: active === item.id
            })} onClick={() => handleUserClick(item)}>
              <Avatar img={profilePath(item.imageid)} referrerPolicy="no-referrer" imgHeight='42' imgWidth='42' />
              <div className='chat-info flex-grow-1 position-relative'>
                <div className='d-flex justify-content-between'>
                  <h5 className='mb-0 text-truncate font-small-4'>{item.name}</h5>
                  {item.createdon && <span className='text-end font-small-2 text-body'> {moment(item.createdon, 'x').format('hh:mm a')}</span>}
                </div>
                <CardText className='text-truncate font-small-3' >{item.message?.replace(/(<([^>]+)>)/ig, '')}</CardText>
                {item.unseenMsgs >= 1 ? (
                  <Badge className='float-end chat-unreadflg' color='danger' pill>
                    {item.unseenMsgs}
                  </Badge>
                ) : null}
              </div>
            </li>
          )
        })
      }
    } else {
      return null
    }
  }

  // ** Handles Filter
  const handleFilter = e => {
    setQuery(e.target.value)
    const searchFilterFunction = contact => contact.name.toLowerCase().includes(e.target.value.toLowerCase())
    const searchFilterGroupFunction = group => group.name.toLowerCase().includes(e.target.value.toLowerCase())
    const filteredChatsArr = groups.filter(searchFilterGroupFunction)
    const filteredContactssArr = contacts.filter(searchFilterFunction)
    setFilteredChat([...filteredChatsArr])
    setFilteredContacts([...filteredContactssArr])
  }

  const [addgroup, setAddgroup] = useState(false)
  const addGroup = () => {
    setAddgroup(true)
  }

  const closeAddGroup = (val) => {
    setAddgroup(val)
  }

  return store ? (
    <div className='sidebar-left'>

      {addgroup && <Chatgroup contacts={contacts} closeAddGroup={closeAddGroup} addgroup={addgroup} />}
      <div className='sidebar'>
        <div
          className={classnames('chat-profile-sidebar', {
            show: userSidebarLeft
          })}
        >
          <header className='chat-profile-header'>
            <div className='close-icon'>
              <X size={14} />
            </div>
            <div className='header-profile-sidebar'>
              <Avatar className='box-shadow-1 avatar-border' img={userProfile.avatar || profileImg} size='xl' />
              <h4 className='chat-user-name'>{userProfile.fullName}</h4>
              <span className='user-post'>{userProfile.role}</span>
            </div>
          </header>
        </div>
        <div
          className={classnames('sidebar-content', {
            show: sidebar === true
          })}
        >
          <div className='sidebar-close-icon' onClick={handleSidebar}>
            <X size={14} />
          </div>
          <div className='chat-fixed-search'>
            <div className='d-flex align-items-center w-100'>
              <div className='sidebar-profile-toggle'>
                {Object.keys(userProfile).length ? (
                  <Avatar
                    className='avatar-border'
                    img={userProfile.driveid ? `https://drive.google.com/thumbnail?id=${userProfile.driveid}&authuser=0` : profileImg}
                    referrerPolicy="no-referrer"
                    imgHeight='42'
                    imgWidth='42'
                  />
                ) : null}
              </div>
              <InputGroup className='input-group-merge ms-1 w-100'>
                <InputGroupText className='round'>
                  <Search className='text-muted' size={14} />
                </InputGroupText>
                <Input
                  value={query}
                  className='round'
                  placeholder='Search or start a new chat'
                  onChange={handleFilter}
                />
              </InputGroup>
            </div>
          </div>
          <PerfectScrollbar className='chat-user-list-wrapper list-group' options={{ wheelPropagation: false }}>
            <div className='chat-list-title d-flex justify-content-between'>
              <h4 className='text-primary'>Groups</h4>
              <PlusCircle size={17} onClick={() => addGroup()} />
            </div>
            <ul className='chat-users-list chat-list media-list'>{renderGroups()}</ul>
            <h4 className='chat-list-title'>Contacts</h4>
            <ul className='chat-users-list contact-list media-list'>{renderContacts()}</ul>
          </PerfectScrollbar>
        </div>
      </div>
    </div>
  ) : null
}

export default SidebarLeft
