import { gapi } from "gapi-script"
import moment from "moment"
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from "reactstrap"
import { ChevronDown } from "react-feather"
import { useEffect, useState } from "react"

const SCOPE = 'https://www.googleapis.com/auth/drive https://www.googleapis.com/auth/drive.readonly https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/gmail.send'
const FilePr = (props) => {
    const { file, userId, item } = props
    const [fileUrl, setfileUrl] = useState('')

    const getFileUrl = async (file) => {
        await gapi.load('client:auth2', async () => {
            await gapi.client.init({
                apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
                clientId: process.env.REACT_APP_GOOGLE_CLIENT_ID,
                scope: SCOPE
            })

            await gapi.client.load("drive", "v3").then(() => {
                try {
                    gapi.client.drive.files
                        .get({ fileId: file?.driveid, fields: 'webContentLink,size' })
                        .then((response) => {
                            const url = response.result.webContentLink
                            setfileUrl(url)

                        })
                        .catch((error) => {
                            console.log(error)
                        })
                } catch (error) {
                    console.log(error)
                }
            })
        })        
    }

    useEffect(() => {
        getFileUrl(file)
    }, [file])

    return (
        <div className={`d-flex flex-wrap flex-column ${item?.touserid !== userId ? 'align-items-end' : 'align-items-start'}`}>
            <UncontrolledDropdown className={item.touserid === userId ? 'reply-icon-left' : 'reply-icon'}>
                <DropdownToggle tag='div'>
                    <ChevronDown size={15} className='cursor-pointer text-body' />
                </DropdownToggle>
                <DropdownMenu>
                    <DropdownItem
                        tag='a'
                        href='/'
                        className='w-100'
                        onClick={e => {
                            e.preventDefault()
                            downloadFile(file.driveid)
                        }}
                    >
                        <span className='align-middle'>Download</span>
                    </DropdownItem>
                </DropdownMenu>
            </UncontrolledDropdown>
            <div className='d-flex flex-column align-items-start border border-2 rounded mb-75 p-1 bg-white'>
                <div className='d-flex flex-column alin-items-start'>
                    <a href={fileUrl} target={'_blank'}>
                        <img className="width-200" src={`https://drive.google.com/thumbnail?id=${file.driveid}&authuser=0`} alt={file?.originalfilename} referrerPolicy="no-referrer" />
                    </a>
                    <span className='font-small-2 m-50'>{file?.originalfilename}</span>
                    <p className='font-small-1 ms-50 text-end mb-0'>{moment(item?.createdon, 'x').format('hh:mm a')} </p>
                </div>

            </div>
        </div >
    )

}

export default FilePr