// ** Third Party Components
import classnames from 'classnames'
import PerfectScrollbar from 'react-perfect-scrollbar'

// ** Reactstrap Imports
import { X, Tag, Clock } from 'react-feather'
import { Input } from 'reactstrap'
import { getChatSearch } from './store/index'
import { useDispatch } from 'react-redux'
import { orgUserId } from '@src/helper/sassHelper'
import { useEffect, useState } from 'react'
import moment from 'moment'
const userId = orgUserId()
const UserProfileSidebar = props => {
  // ** Props
  const { handleUserSidebarRight, userSidebarRight, store } = props

  const { selectedUser, searchList } = store
  const dispatch = useDispatch()
  const [searchData, setSearchData] = useState([])
  const serachList = (val) => {
    const obj = { toUserId: selectedUser?.id, q: val, fromUserId: userId }
    dispatch(getChatSearch(obj))
  }

  useEffect(() => {
    setSearchData(searchList)
  }, [searchList])

  return (
    <div className={classnames('user-profile-sidebar col-md-3 col-sm-12', { show: userSidebarRight === true })}>
      <header className='user-profile-header'>
        <div className='d-flex justify-content-between border-1 border pt-1 px-1 pb-50 align-items-center'>
          <h6 className='text-body'>Search Messages</h6>
          <span className='close-icon' onClick={() => { handleUserSidebarRight(); setSearchData([]) }}>
            <X size={14} />
          </span>
        </div>
        <div className='header-profile-sidebar px-1' style={{ height: '55px' }}>
          <Input type='text' placeholder='Search' onInput={(e) => { serachList(e.target.value) }} />
        </div>
      </header>
      <PerfectScrollbar className='user-profile-sidebar-area' options={{ wheelPropagation: false }}>

        <div className='more-options'>
          {searchData.length === 0 && <p className='text-center mt-2'>Search for message with your self</p>}
          {searchData.length > 0 &&
            <ul className='list-unstyled'>
              {searchData.map((item, k) => {
                return (
                  <li className='cursor-pointer mt-50' key={k}>
                    <div className='chat-body me-1'>

                      <div className='chat-content rounded' style={{ backgroundImage: 'linear-gradient(80deg, #7367f0, #9e95f5)' }}>
                        <span className='font-small-2 text-white pt-25 ps-25'>{moment(item.createdon, 'x').fromNow()}</span>
                        <p className='text-white text-truncate p-50 m-0'>
                          {item.message?.replace(/(<([^>]+)>)/ig, '')}
                        </p>
                      </div></div>
                  </li>)
              })
              }
            </ul>
          }
        </div>
      </PerfectScrollbar>
    </div>
  )
}

export default UserProfileSidebar
