// ** React Imports
import { Fragment, useState, useEffect } from 'react'

// ** Chat App Component Imports
import Chat from './Chat'
import Sidebar from './SidebarLeft'
import UserProfileSidebar from './UserProfileSidebar'
import GroupInfo from './GroupInfo'
// ** Third Party Components
//import classnames from 'classnames'
// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'
import { getChatContacts, getUserProfile, unreadCount, getChatGroups } from './store'

import '@styles/base/pages/app-chat.scss'
import '@styles/base/pages/app-chat-list.scss'

const AppChat = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.chat)

  // ** States
  const [user, setUser] = useState({})
  const [sidebar, setSidebar] = useState(false)
  const [userSidebarRight, setUserSidebarRight] = useState(false)
  const [userGroupInfoRight, setGroupInfoRight] = useState(false)
  const [userSidebarLeft, setUserSidebarLeft] = useState(false)
  const [updateSelectedUser, setUpdateSelectedUser] = useState({})

  // ** Sidebar & overlay toggle functions
  const handleSidebar = () => setSidebar(!sidebar)
  const handleUserSidebarLeft = () => setUserSidebarLeft(!userSidebarLeft)
  const handleUserSidebarRight = () => setUserSidebarRight(!userSidebarRight)
  const handleGroupInfoSidebarRight = () => setGroupInfoRight(!userGroupInfoRight)
  const handleOverlayClick = () => {
    setSidebar(false)
    setUserSidebarRight(false)
    setUserSidebarLeft(false)
  }

  const handleOverlayClickGroupInfo = () => {
    setSidebar(false)
    setGroupInfoRight(false)
    //setUserSidebarLeft(false)
  }

  const userUpdate = (val) => {
    setUpdateSelectedUser(val)
  }

  useEffect(() => {
    setUpdateSelectedUser(store.selectedUser)
    handleOverlayClickGroupInfo()
    handleOverlayClick()
  }, [store.selectedUser])

  // ** Set user function for Right Sidebar
  const handleUser = obj => setUser(obj)

  // ** Get data on Mount
  useEffect(async () => {
    await dispatch(getChatContacts())
    await dispatch(unreadCount())
    await dispatch(getUserProfile())
    await dispatch(getChatGroups())
  }, [])


  const refreshList = async (val) => {
    if (val) {
      await dispatch(getChatGroups())
    }
  }

  return (
    <Fragment>
      <Sidebar
        store={store}
        sidebar={sidebar}
        handleSidebar={handleSidebar}
        userSidebarLeft={userSidebarLeft}
        handleUserSidebarLeft={handleUserSidebarLeft}
      />
      <div className='content-right'>
        <div className='content-wrapper'>
          <div className='content-body'>
            <Chat
              store={store}
              handleUser={handleUser}
              handleSidebar={handleSidebar}
              userSidebarLeft={userSidebarLeft}
              userSidebarRight={userSidebarRight}
              handleUserSidebarRight={handleUserSidebarRight}
              userGroupInfoRight={userGroupInfoRight}
              updateSelectedUser={updateSelectedUser}
              handleGroupInfoSidebarRight={handleGroupInfoSidebarRight}
            />
            {userSidebarRight &&
              <UserProfileSidebar
                user={user}
                store={store}
                handleOverlayClick={handleOverlayClick}
                userSidebarRight={userSidebarRight}
                handleUserSidebarRight={handleUserSidebarRight}
              />
            }
            {userGroupInfoRight &&

              <GroupInfo
                user={user}
                userUpdate={userUpdate}
                updateSelectedUser={updateSelectedUser}
                store={store}
                handleOverlayClickGroupInfo={handleOverlayClickGroupInfo}
                userGroupInfoRight={userGroupInfoRight}
                handleGroupInfoSidebarRight={handleGroupInfoSidebarRight}
                refreshList={refreshList}
              />

            }
          </div>
        </div>
      </div>
    </Fragment>
  )
}

export default AppChat
